body {
  color: #fff;
  background: black;
}

.wrap {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-height: 398px;
  min-width: 836px;
  width: 836px;
  height: 398px;

  .wrap_controls {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 9999;

    &-left,
    &-right {
      height: 40px;
      width: 40px;
      position: absolute;
      top: 45%;
      z-index: 9999;
      transform: translate(-50%, 0);
      cursor: pointer;
    }
    &-left {
      background-image: url("../img/left.svg");
      left: 2rem;
    }
    &-right {
      background-image: url("../img/right.svg");
      right: -1.5rem;
    }
  }

  .video {
    position: absolute;
    top: 0;
    left: 0;
    transition: left 0.25s ease;
    width: 836px;
    height: 398px;

    &:first-child {
      z-index: 0;
    }

    &:nth-child(2) {
      left: 75px;
      z-index: -1;
      transform: scale(0.9);
    }

    &:nth-child(3) {
      left: 150px;
      z-index: -2;
      transform: scale(0.8);
    }

    &:nth-child(4) {
      left: 225px;
      z-index: -3;
      transform: scale(0.7);
    }
  }
}

.push-forward {
  transform: scale(1) translate(-110%, 0) !important;
}
.push-back {
  transform: translate(0, 0);
  z-index: 999;
}
.block-btn {
  pointer-events: none;
  opacity: 0.5;
}


